@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #fef6ef;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.flex-wrapper{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.flex-wrapper>#cb-wrapper{width:40%;max-width:400px}.flex-wrapper>#cb-wrapper>#coffee-bk{width:100%;height:auto;margin:auto}.flex-wrapper>#bt-wrapper>#start-bt{font-family:"JetBrains Mono",monospace;color:#fee1ca;font-size:3rem;font-feature-settings:none;font-variant-ligatures:none;background:linear-gradient(to left, #987961 50%, #FECBA3 50%) right;background-size:200%;font-weight:bolder;box-shadow:0 2px 4px 2px rgba(72,58,42,.5),0 1px #fff;border-radius:1.5vw;padding:.5vh 3vw;transition:.3s ease-out}.flex-wrapper>#bt-wrapper>#start-bt:hover{background-position:left;cursor:pointer;font-family:"JetBrains Mono",monospace;color:#6c5745;font-size:3rem;font-feature-settings:none;font-variant-ligatures:none}
.cell{height:8vw;width:8vw;max-height:70px;max-width:70px;text-align:center;line-height:70px;box-shadow:0 2px 2px #91745d inset,0 2px #91745d;border-radius:5%}.type2{background-color:#a3d6fe}.type4{background-color:#cba3fe}.type8{background-color:#fecba3}.type16{background-color:#a3feca}.type32{background-color:#fdd269}.type64{background-color:#feeba3}.type128{background-color:#feaaa3}.type256{background-color:#a6a3fe}.type512{background-color:#fea866}.type1024{background-color:#6c5745;color:#fef6ef}.type2048{background-color:#241c17;color:#fef6ef}
.board-wrapper{display:flex;justify-content:center;margin-right:20px}.board-wrapper>.board{display:flex;flex-direction:row;flex-flow:row wrap;width:64vw;max-width:560px;background-color:#feefe3;border-radius:1%;box-shadow:0 2px 4px rgba(0,0,0,.5) inset,0 1px #fff}
#side-wrapper{display:flex;flex-direction:column;align-items:center;width:30vw;max-width:360px;background-color:#fee1cb;border-radius:1%;box-shadow:0 2px 4px rgba(0,0,0,.5) inset,0 1px #fff;margin-left:20px;justify-content:space-evenly}#side-wrapper>section>.side-button{width:20vw;max-width:250px;padding:10px 5px 10px 5px;background-color:#fec0a3;text-align:center;overflow-wrap:normal;border-radius:3%;box-shadow:0 2px 4px 2px rgba(72,58,42,.5),0 1px #fff}#side-wrapper>section>.side-button>#construct{font-family:"JetBrains Mono",monospace;color:#241c17;font-size:18px;font-feature-settings:none;font-variant-ligatures:none;font-weight:bolder}#side-wrapper>section>.side-button>#data{font-family:"JetBrains Mono",monospace;color:#483a2e;font-size:25px;font-feature-settings:none;font-variant-ligatures:none}
#master{background-color:#fee1cb;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}#master>#game-wrapper{background-color:#6c5745;width:95vw;max-width:1100px;margin-left:auto;margin-right:auto;padding:50px;display:flex;justify-content:center;border-radius:1%;box-shadow:0 2px 4px rgba(0,0,0,.5) inset,0 1px #fff}#overlay{opacity:0;position:fixed;margin:auto;height:95vh;width:95vw;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,.85)}#overlay>#ui-container{position:fixed;margin:auto;height:70vh;width:35vw;max-height:600px;max-width:480px;top:0;right:0;bottom:0;left:0;background-color:#fee2dc;border-radius:12px;display:flex;flex-direction:column;align-items:center}#overlay>#ui-container>#message{width:26vw;max-width:350px;font-weight:bolder;text-align:center;margin:11.5% auto auto;background-color:#fec0a3;padding:10px;border-radius:3%;box-shadow:0 2px 4px 2px rgba(72,58,42,.5),0 1px #fff;font-family:"JetBrains Mono",monospace;color:#6c5745;font-size:2rem;font-feature-settings:none;font-variant-ligatures:none}#overlay>#ui-container>#stat{flex:1 1;display:flex;flex-direction:column;padding-top:9%}#overlay>#ui-container>#stat>#points-stat,#overlay>#ui-container>#stat #moves-stat{font-weight:bolder;text-align:center;background-color:#feaaa3;padding:10px;box-shadow:0 2px 4px 2px rgba(72,58,42,.5),0 1px #fff;margin:5% 0;font-family:"JetBrains Mono",monospace;color:#6c5745;font-size:1.75rem;font-feature-settings:none;font-variant-ligatures:none}#overlay>#ui-container>#button-container{display:flex;flex-direction:row;width:35vw;max-width:480px;justify-content:center;padding-bottom:7.5%}#overlay>#ui-container>#button-container>#retry-button,#overlay>#ui-container>#button-container #next-button{font-weight:bolder;width:40%;max-width:165px;text-align:center;background:linear-gradient(to left, #FEC0A3 50%, #FECBA3 50%) right;background-size:200%;transition:.3s ease-out;padding:10px;border-radius:3%;box-shadow:0 2px 4px 2px rgba(72,58,42,.5),0 1px #fff;margin:3%;font-family:"JetBrains Mono",monospace;color:#6c5745;font-size:1.5rem;font-feature-settings:none;font-variant-ligatures:none}#overlay>#ui-container>#button-container>#retry-button:hover,#overlay>#ui-container>#button-container #next-button:hover{background-position:left;cursor:pointer;font-family:"JetBrains Mono",monospace;color:#feefe3;font-size:1.5rem;font-feature-settings:none;font-variant-ligatures:none}
